.bag-for-every {
	padding: 22px 0 20px;
	position: relative;	
	z-index: 3;
	.title {
		padding-left: 23px;
		max-width: 1060px;
		margin-bottom: 75px;
		.icon {
			top: 3px;
		}
		h3 {
			@media only screen and (max-width: 1919px) {
				color: #fff;
			}
		}
		@include msm() {
			padding-left: 0;
		}
	}
	@media only screen and (max-width: 1919px) {
		background-color: #284690;
	}
	@include mmd() {
	}
}

.bag-for-every__main-bg {
	position: absolute;
	top: 205px;
	left: 0;
	width: 102%;
	max-width: none;
	height: 2650px;
}
.bag-for-every__bg-part {
	position: absolute;
	top: 527px;
	left: 0;
}
.bag-for-every__bg-part2 {
	position: absolute;
	bottom: 270px;
	right: 10px;
}
.bag-for-every__cloud {
	position: absolute;
	left: 96%;
	top: 760px;
	transform: rotateY(-180deg);
	@include mmd() {
		display: none;
	}
}
.bag-for-every__cloud_second {
	top: 1615px;
	right: auto;
	left: 60px;
}

.bag-for-every__list {
	padding-right: 100px;
	margin-bottom: 100px;
	@media only screen and (max-width: 1919px) {
	}
	@include mlg() {
		padding-right: 0;
	}
	.bag-for-every__item {
		position: relative;
		width: 100%;
		font-size: 20px;
		line-height: 30px;
		color: #34353c;
		font-weight: 600;

		background-color: #fff;

		padding: 20px 35px 90px;
		@include mmd() {
			padding: 90px 20px 20px;
		}
		@include msm() {
		}
		p {
			margin-bottom: 0;
		}
		.icon {
			position: absolute;
			bottom: 20px;
			@include mmd() {
				left: 20px !important;
				right: auto !important;
				bottom: auto;
				top: 20px;
			}
			@include msm() {
				right: 5px !important;
			}
		}
		&:nth-child(4n+1) {
			background-color: #f24559;
			color: #fff;

			@include lg() {
				flex: 0 0 45%;
				max-width: 45%;
				min-height: 230px;
			}
			.icon {
				left: 30px;
			}
			@include mxl() {
			}
		}
		&:nth-child(4n+2) {

			@include lg() {
				flex: 0 0 45.5%;
				max-width: 45.5%;
				min-height: 180px;
			}
			.icon {
				right: 26px;
			}
			@include mxl() {
			}
		}
		&:nth-child(4n+3) {
			@include lg() {
				flex: 0 0 41.2%;
				max-width: 41.2%;
				min-height: 175px;
			}
			background-color: #fff;
			.icon {
				left: 20px;
			}
			@include mxl() {
			}
		}
		&:nth-child(4n+4) {
			@include lg() {
				flex: 0 0 45.5%;
				max-width: 45.5%;
				min-height: 270px;
			}
			background-color: #3c509e;
			color: #fff;
			.icon {
				right: 25px;
			}
			@include mxl() {
			}
		}
	}
}
.bag-for-every__list-in {
	width: 100%;
	flex: 0 0 1030px;
	max-width: 1030px;
	@include mxl() {
		max-width: none;
		flex: 1 1 auto;
	}
}


.bag-for-every__list-reverse {
	padding-left: 60px;
	margin-bottom: 90px;
	@include mlg() {
		padding-left: 0;
		margin-bottom: 20px;
	}
	.bag-for-every__list-in {
		flex: 0 0 1045px;
		max-width: 1045px;
		@media only screen and (max-width: 1919px) {
		}
		@include mxl() {
			max-width: none;
			flex: 1 1 auto;
		}
		.row {
		}
	}
	.bag-for-every__item {
		position: relative;
		background-color: #fff;
		color: #34353c;
		font-size: 20px;
		line-height: 30px;
		width: 100%;
		
		padding: 20px 35px 90px;
		@include mmd() {
			padding: 90px 20px 20px;
			height: auto !important;
		}
		p {
			margin-bottom: 0;
		}
		.icon {
			position: absolute;
			bottom: 20px;
			@include mmd() {
				left: 10px !important;
				right: auto !important;
				bottom: auto;
				top: 10px;
			}
			@include msm() {
				right: 5px !important;
			}
		}
		&:nth-child(4n+1) {
			@include lg() {
				flex: 0 0 46.3%;
				max-width: 46.3%;
				height: 255px;
			}
			margin-top: auto;
			.icon {
				left: 21px;
			}
		}
		&:nth-child(4n+2) {
			background-color: #f24559;
			@include lg() {
				max-width: 53.2%;
				flex: 0 0 53.2%;
				height: 260px;
			}
			color: #fff;
			.icon {
				right: 16px;
			}
		}
		&:nth-child(4n+3) {
			background-color: #3c509e;
			@include lg() {
				flex: 0 0 46.3%;
				max-width: 46.3%;
				height: 180px;
			}

			color: #fff;
			.icon {
				left: 33px;
			}
		}
		&:nth-child(4n+4) {
			background-color: #fff;
			@include lg() {
				flex: 0 0 47.3%;
				max-width: 47.3%;
				height: 240px;
				
			}
			margin-bottom: auto;
			.icon {
				right: 10px;
			}
		}
	}
}

