img {
	max-width: 100%;
	height: auto;
}

a {
	text-decoration: none;
	color: #333;
	&:hover {
		color: #333;
	}
	&:focus {
		outline: none;
	}
}
html {
	
}
body {
	font-family: $font;
	font-weight: 400;
	font-size: 18px;
	line-height: 1.15;
	color: #34353c;
	position: relative;
}

.pixelperfect {
	position: absolute;
	top: 0;
	left: 0;
	background: url("../images/main3.jpg") 0 0 no-repeat;
	z-index: 0;
	width: 100%;
	height: 100%;
	opacity: .3;
}
.container-fluid {
	max-width: 1330px;
	margin-left: auto;
	margin-right: auto;
}


.col-full-mob {
	@media only screen and(max-width: 400px) {
		flex: 0 0 100%;
		max-width: 100%;
	}
}



.table-wrap {
	overflow: auto;
}
.padding {
	padding-left: 15px;
	padding-right: 15px;
}
.no-paddings {
	padding-left: 0;
	padding-right: 0;
}
.list-clr {
	ul {
		padding: 0;
		margin: 0;
		list-style: none;
	}
}
.icon {
	display: inline-block;
	margin-right: 10px;
}
.icon-minus {
	display: block;
	width: 55px;
	height: 55px;
	border: 1px solid #000;
	border-radius: 50%;
	position: relative;
	&:before {
		content: '';

		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		background-color: #000;
		height: 3px;
		width: 33px;
	}
}
.icon-plus {
	display: block;
	width: 55px;
	height: 55px;
	border: 1px solid #000;
	border-radius: 50%;
	position: relative;
	&:before {
		content: '';

		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		background-color: #000;
		height: 3px;
		width: 33px;
	}
	&:after {
		content: '';
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		width: 3px;
		height: 33px;
		background-color: #000;
	}
}
.icon-heart-colored, .icon-heart-white {
	animation-name: bounceIn;
	animation-duration: 3s;
	animation-iteration-count: infinite;
}
.icon-plus.icon-plus_white, .icon-minus.icon-minus_white {
	border-color: #fff;
	&:before, &:after {
		background-color: #fff;
	}
}
.icon-plus.icon-plus_blue, .icon-minus.icon-minus_blue {
	border-color: #3c509e;
	&:before, &:after {
		background-color: #3c509e;
	}
}
.icon-plus.icon-plus_red, .icon-minus.icon-minus_red {
	border-color: #f24559;
	&:before, &:after {
		background-color: #f24559;
	}
}
input, textarea {
	&:focus {
		outline: none;
	}
}
button, input[type="submit"] {
	cursor: pointer;
	&:focus {
		outline: none;
	}
}

