.circleMove {
	animation-name: circleMove;
	animation-duration: 10s;
	animation-iteration-count: infinite;
}
@-webkit-keyframes circleMove {
	from {
		transform: rotate(0deg)
			translate(-400px)
			rotate(0deg);
	}
	to {
		transform: rotate(360deg)
			translate(-400px) 
			rotate(-360deg);
	}
}
@-o-keyframes circleMove {
	from {
		transform: rotate(0deg)
			translate(-400px)
			rotate(0deg);
	}
	to {
		transform: rotate(360deg)
			translate(-400px) 
			rotate(-360deg);
	}
}
@-moz-keyframes circleMove {
	from {
		transform: rotate(0deg)
			translate(-400px)
			rotate(0deg);
	}
	to {
		transform: rotate(360deg)
			translate(-400px) 
			rotate(-360deg);
	}
}
@keyframes circleMove {
	from {
		transform: rotate(0deg)
			translate(-400px)
			rotate(0deg);
	}
	to {
		transform: rotate(360deg)
			translate(-400px) 
			rotate(-360deg);
	}
}
.circleMove2 {
