.modal {

}
.modal-title {
	font-size: 20px;
	text-transform: uppercase;
	color: #8d8b8e;
	font-weight: 700;
}
.modal-body {
	padding: 40px 20px 20px;
	input {
		font-size: 16px;
		line-height: 1;
		color: #817f82;
		font-family: 'Lato',sans-serif;
		font-weight: 400;

		width: 100%;
		background-color: transparent;
		padding: 0 0 8px;
		border: none;
		border-bottom: 1px solid #dbd9dc;
		margin-bottom: 20px;
		@include iph() {
			font-size: 16px;
			line-height: 1;
			color: #817f82;
			font-family: 'Lato',sans-serif;
			font-weight: 400;
		}
		&:last-child {
			margin-bottom: 0;
		}
		@include mmd() {
			margin-bottom: 25px;
		}
	}
	button {
		display: inline-block;
		padding: 10px;
		
		font-size: 14px;
		line-height: 1;
		font-weight: 700;
		color: #fff;

		background-color: #778cf7;
		border-radius: 3px;
		border: none;
		margin-left: auto;
		transition: ease .2s;
		&:hover {
			background-color: #3b64df;
		}
	}
}

